import { getParent, types } from 'mobx-state-tree'

const interval = 2000

const FullScreenModel = types
  .model({
    selectedImage: types.optional(types.number, -1),
    autoPlay: types.optional(types.boolean, false),
  })
  .actions((state) => {
    let counter = 0
    return {
      enterFullScreen(selectedImage) {
        state.selectedImage = selectedImage
        state.autoplay && state.initCounter()
      },
      initCounter() {
        clearInterval(counter)
        counter = setInterval(state.nextImage, interval)
      },
      exitFullScreen() {
        state.selectedImage = -1
        clearInterval(counter)
      },
      nextImage() {
        const nextImage = state.selectedImage + 1
        const imageListLength = getParent(state).imageList.length
        state.selectedImage = nextImage < imageListLength ? nextImage : 0
      },
      prevImage() {
        const nextImage = state.selectedImage - 1
        const lastImageIdx = getParent(state).imageList.length - 1
        state.selectedImage = nextImage > -1 ? nextImage : lastImageIdx
      },
    }
  })
  .views((state) => ({
    get currentImage() {
      return state.selectedImage === -1
        ? undefined
        : getParent(state).imageList[state.selectedImage]
    },
  }))

export default FullScreenModel
